@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;300;400;500;700&display=swap);
/* Universal Styles */

html {
  font-size: 18px;
  background-color: #222;
  font-family: "Noto Sans JP", sans-serif;
}

@media only screen and (max-width: 560px) {
  html {
    font-size: 15px;
  }
}

input:focus {
  outline: none;
}

/* Reusable Component - Image Container */

.image-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow: hidden;
  background: #000;
}

.image-container img {
  width: 100%;
}

/* App Styles */

.App {
  font-family: "Noto Sans JP", "Work Sans", sans-serif;
}

h1 {
  padding: 0.66rem 0;
  text-align: center;
  letter-spacing: 10px;
  background-color: #222;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 300;
  font-size: 1.8rem;
  color: #fff;
}

h6 {
  padding: 0.66rem 0;
  text-align: center;
  letter-spacing: 5px;
  background-color: #222;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 100;
  font-size: 0.5rem;
  color: #fff;
}

.header {
  height: 3.2rem;
  align-items: center;
  margin: 0 auto;
}

.footer {
  color: #fff;
  text-align: center;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 100;
  font-size: 0.5rem;
  margin-bottom: 1rem;
  letter-spacing: 3px;
}

.footer a {
  color: #aaa;
  text-decoration: none;
  letter-spacing: 2px;
}

.footer a:hover {
  color: #fff;
}

.BusinessList {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 4.4rem 10%;
}
.Business {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: #fff;
  font-family: "Noto Sans JP", sans-serif;
  width: 16.66rem;
  margin: 0 0.5rem 2.3rem 0.5rem;
}

.Business .image-container {
  height: 16.66rem;
  margin-bottom: 1rem;
}

.Business .image-container img {
  height: 16.66rem;
  object-fit: cover;
}

.Business h2 {
  margin-bottom: 0.5rem;
  font-size: 1.2rem;
  font-weight: 600;
}

.Business-information {
  display: flex;
  justify-content: space-between;
}

.Business-information p {
  font-size: 0.88rem;
  font-weight: 300;
  line-height: 1rem;
}

.Business-address {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.Business-reviews {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: right;
}

.Business-reviews h3 {
  color: #fff;
  font-weight: 600;
}

.Business-reviews .rating {
  font-size: 0.88rem;
  line-height: 1rem;
}

.Business a {
  color: #ffffff;
  text-decoration: none;
}

.Business a:hover {
  color: #999;
}

.SearchBar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-image: url(/givemecoffee.zorbajobswong.com/static/media/background_search_desktop.5bf45382.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center top;
  height: 40rem;
}

.SearchBar-sort-options ul {
  display: flex;
  justify-content: center;
  margin-bottom: 2.22rem;
  color: #ffffff;
}

.SearchBar-sort-options li {
  cursor: pointer;
  width: 4.33rem;
  border-bottom: 1px solid #222;
  padding: 0 2.58rem 0.33rem 2.58rem;
  line-height: 1.13;
  text-align: center;
  font-weight: 600;
  font-size: 0.83rem;
  transition: color 0.25s;
}

.SearchBar-sort-options li:hover {
  color: #222;
  border-bottom: 1px solid #222;
}

.SearchBar-sort-options li.active,
.SearchBar-sort-options li.active:hover {
  border-bottom: 1px solid #fff;
  color: #fff;
}

.SearchBar-fields {
  display: flex;
  justify-content: center;
  margin-bottom: 2.88rem;
}

.SearchBar-fields input {
  width: 21rem;
  padding: 0.66rem 1rem;
  margin-right: 2.22rem;
  border: 1px solid #222;
  border-radius: 4px;
  font-size: 0.77rem;
  font-weight: 500;
}

.SearchBar-fields input:last-child {
  margin-right: 0;
}

.SearchBar-submit {
  text-align: center;
}

.SearchBar-submit a {
  border-radius: 4px;
  padding: 0.72rem 1.7rem;
  background-color: #222;
  color: #ffffff;
  font-weight: 600;
  transition: background-color 0.5s;
}

.SearchBar-submit a:hover {
  cursor: pointer;
  background-color: #fff;
  color: #222;
}

@media only screen and (max-width: 560px) {
  .SearchBar {
    background-image: url(/givemecoffee.zorbajobswong.com/static/media/background_search_mobile.28b9a920.jpg);
  }

  .SearchBar-sort-options ul {
    margin-left: 2rem;
    margin-right: 2rem;
  }

  .SearchBar-fields {
    flex-direction: column;
    align-items: center;
  }

  .SearchBar-fields input {
    margin-right: 0;
    margin-bottom: 0.86rem;
  }
}

